@import '../../assets/scss/color';

.sidebar{
    background: $sidebar-background;
    width: 230px;
    color: #ffffff;
}
.top-nav{
    background: $topnav-background;
    padding: 10px;
}
.user{
    padding-bottom: 25px;
    display: flex;
    align-items: center;
    .user-avatar{
        img{
            width: 45px;
            border-radius: 100%;
            display: block;
        }
    }
    .user-info{
        margin-left: 20px;
        .user-name{
            font-size: 13px;
            font-weight: bold;
            padding-bottom: 10px;
            margin-bottom: 0;
        }
        .user-status{
            display: flex;
            margin-bottom: 0;
            &-round{
                background: $userstatus-color;
                border-radius: 100%;
                width: 10px;
                height: 10px;
                display: block;
                margin-right: 5px;
            }
            &-text{
                font-size: 11px;
            }
        }
    }
}
.search{
    background: $search-color;
    display: flex;
    height: 35px;
    border-radius: 2px;
    border: 1px solid $search-color;
    .search-input{
        background: transparent;
        border: none;
        padding-left: 10px;
        font-size: 14px;
        color: #7b746d;
    }
    .search-icon{
        display: flex;
        align-items: center;
        color: #7b746d;
    }
}
.sidebar{
    .title{
        background: $mainnav-background;
        font-size: 12px;
        text-transform: uppercase;
        color: $title-color;
        padding: 15px;
    }
    .list{
        cursor: pointer;
    }
}
.main-nav{
    .item{
        background: $topnav-background;
        display: flex;
        padding: 12px 20px;
        justify-content: space-between;
        color: $item-color;
        &:hover{
            background: $mainnav-background;
        }
    }
    .item.active{
        border-left: 3px solid #3c8dbc;
        background: $mainnav-background;
    }
}
.item-left{
    display: flex;
    align-items: center;
    .item-left-icon{
        position: relative;
        &-primary{
            color: $primary;
        }
        &-danger{
            color: $danger;
        }
        &-warning{
            color: $warning;
        }
    }
    .content{
        font-size: 13px;
        padding-left: 10px;
    }
}
.item-right-icon{
    .box{
        padding: 3px 5px;
        border-radius: 2px;
        color: #ffffff;
        font-size: 10px;
        margin-left: 5px;
        &-primary{
            background: $primary;
        }
        &-success{
            background: $success;
        }
        &-danger{
            background: $danger;
        }
        &-warning{
            background: $warning;
        }
    }
}
@media screen and (max-width: 768px) {
    .sidebar{
        display: none;
    }
}